	background-color: var(--vscode-notebook-cellHoverBackground);
}

/** Cell symbol higlight */
.monaco-workbench
	.notebookOverlay
	.monaco-list
	.monaco-list-row.code-cell-row.nb-symbolHighlight
	.cell-focus-indicator,
.monaco-workbench
	.notebookOverlay
	.monaco-list
	.monaco-list-row.markdown-cell-row.nb-symbolHighlight {
